import { Component, OnInit } from '@angular/core';
import { QuestionarioService } from 'src/app/services/questionario.service';
import { ModalController } from '@ionic/angular';
import { PacienteResponderDiaComponent } from 'src/app/components/paciente-responder-dia/paciente-responder-dia.component';
import { SessaoService } from 'src/app/services/sessao.service';
import { PacienteResponderSessaoComponent } from 'src/app/components/paciente-responder-sessao/paciente-responder-sessao.component';
import { PacienteEntendendoFumoComponent } from 'src/app/components/paciente-entendendo-fumo/paciente-entendendo-fumo.component';
import { PacienteService } from 'src/app/services/paciente.service';
import { Paciente } from 'src/app/models/paciente';
import { Sessao } from 'src/app/models/sessao';
import { NavExtrasService } from 'src/app/services/nav-extras.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-perfil',
  templateUrl: './perfil.page.html',
  styleUrls: ['./perfil.page.scss'],
})
export class PerfilPage implements OnInit {

  paciente = new Paciente;

  sessoes:Sessao[] = [];

  constructor(private pacienteService:PacienteService, private sessaoService:SessaoService, private navExtra:NavExtrasService, private router:Router) { }

  async ngOnInit() {
    this.paciente = await this.pacienteService.getPacienteLogado();
    this.sessoes = await this.sessaoService.buscarSessoes(this.paciente);
  }

  editar() {
    this.navExtra.set('paciente', this.paciente);
    this.router.navigateByUrl('/paciente/perfil/edicao');
  }

}
